body {
  font-family: Arial, sans-serif;
}

.select-container {
  width: 300px;
  margin: 0 auto;
}

.cat-info {
  max-width: 1200px;
  gap: 20px;
  margin: 20px auto 0;
  display: flex;
}

.cat-info h2 {
  margin: 0;
  font-size: 36px;
}

.cat-info p {
  font-size: 24px;
}

.cat-info.hidden, .loader.hidden, .breed-select.hidden {
  display: none;
}

.breed-select {
  font-size: 24px;
}

.loader {
  width: 60px;
  height: 60px;
  box-sizing: border-box;
  border: 5px solid #333;
  border-bottom-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: absolute;
  top: 20%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

